@media (min-width: 71.25em) {
  .main {
    display: grid;
    grid-template-areas: "one one img img" "two two img img" "two two img img" "tre tre img img";
  }
}
.main__header {
  margin: 1.5625rem 0 1.25rem 1.5625rem;
}
.main__header img {
  max-width: 100%;
  height: auto;
}
@media (min-width: 71.25em) {
  .main__header {
    grid-area: one;
    margin: 2.5rem 0 3.75rem 7.8125rem;
  }
  .main__header img {
    width: 160px;
    height: 40px;
  }
}
.main__image {
  padding: 0.3125rem 0 0.625rem 0;
  margin: 0.625rem 0 0.625rem 0;
  font-size: 0;
}
.main__image img {
  max-width: 100%;
  height: auto;
}
@media (min-width: 71.25em) {
  .main__image {
    grid-area: img;
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 71.25em) {
  .main__left {
    padding: 1.25rem;
    margin-left: 6.25rem;
    grid-area: two;
  }
}
.main__heading {
  text-align: center;
  letter-spacing: 0.625rem;
}
@media (min-width: 71.25em) {
  .main__heading {
    text-align: start;
  }
}
.main__heading .first {
  font-weight: 300;
  font-size: 2.8125rem;
  text-transform: uppercase;
  color: var(--desaturatedRed);
}
@media (min-width: 71.25em) {
  .main__heading .first {
    text-align: start;
  }
}
.main__heading .second {
  text-align: center;
  text-transform: uppercase;
  color: var(--darkGrayishRed);
}
@media (min-width: 71.25em) {
  .main__heading .second {
    text-align: start;
    padding-bottom: 1.5625rem;
  }
}
.main__para {
  text-align: center;
  padding: 0.625rem 1.25rem 0.9375rem 1.25rem;
}
.main__para p {
  font-weight: 400;
  line-height: 1.6;
}
@media (min-width: 71.25em) {
  .main__para {
    text-align: left;
    margin-right: 12.625rem;
    padding: 0 6.25rem 0 0;
    width: 60ch;
  }
}
@media (min-width: 71.25em) {
  .main__email {
    margin-top: 6.25rem;
  }
}
.main__email form {
  display: flex;
  flex-direction: row;
  border: 1px solid var(--desaturatedRed);
  border-radius: 1.5625rem;
  margin: 0.625rem;
  transition: border 0.2s ease;
}
@media (min-width: 71.25em) {
  .main__email form {
    width: 27.1875rem;
    margin: 0 0 0 -0.3125rem;
  }
}
.main__email form input[type=email] {
  border: none;
  flex-grow: 1;
  background: none;
  padding: 0.625rem;
  font-size: 1rem;
}
.main__email form input[type=email]::placeholder {
  color: var(--desaturatedRed);
  opacity: 0.8;
  padding: 0.625rem;
  font-size: 0.9375rem;
}
.main__email form input[type=email]:focus {
  outline: none;
}
.main__email form #icon-error {
  margin-top: 0.4375rem;
  margin-right: 0.625rem;
  display: none;
  transition: display 0.3s ease-in;
}
.main__email form #icon-error img {
  max-width: 100%;
}
.main__email form button {
  background: linear-gradient(135deg, var(--linearGradient-1) 0%, var(--linearGradient-2) 0%, var(--linearGradient-3) 15%, var(--linearGradient-4) 35%);
  font-size: 1.0625rem;
  border: none;
  width: 4.375rem;
  height: 3rem;
  border-radius: 1.5625rem;
  cursor: pointer;
  padding: 0.625rem;
  margin: -0.125rem;
  box-shadow: 0px 8px 8px 1px var(--linearGradient-3);
  transition: all 0.3s ease-in;
}
@media (min-width: 71.25em) {
  .main__email form button {
    width: 5.625rem;
  }
}
.main__email form button:hover {
  background: linear-gradient(135deg, var(--linearGradient-1) 0%, var(--linearGradient-2) 0%, var(--linearGradient-3) 20%, var(--linearGradient-4) 100%);
  box-shadow: 0px 11px 11px 4px var(--linearGradient-3);
}
.main__email #text-error {
  display: none;
  color: var(--softRed);
  margin: 0.9375rem;
  transition: display 0.3s ease-in;
  transition: color 0.2s ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Josefin);
  font-size: 1rem;
}

:root {
  --desaturatedRed: hsl(0, 36%, 70%);
  --softRed: hsl(0, 93%, 68%);
  --darkGrayishRed: hsl(0, 6%, 24%);
  --linearGradient-1: hsl(0, 0%, 100%);
  --linearGradient-2: hsl(0, 100%, 98%);
  --linearGradient-3: hsl(0, 80%, 86%);
  --linearGradient-4: hsl(0, 74%, 74%);
}

h1 {
  font-weight: 300;
  font-size: 2.625rem;
}
@media (min-width: 71.25em) {
  h1 {
    font-size: 3.125rem;
  }
}

h2 {
  font-weight: 600;
  font-size: 2.625rem;
}
@media (min-width: 71.25em) {
  h2 {
    font-size: 3.125rem;
  }
}

p {
  font-size: 1rem;
  font-weight: 300;
  color: var(--desaturatedRed);
}
@media (min-width: 71.25em) {
  p {
    font-size: 1.125rem;
  }
}

:root {
  --font-Josefin: "Josefin Sans", sans-serif;
}